.about-us {
  @include pageGrid;

  &__title {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 1 / 4;
    }

    @include onDesktop {
      grid-column: span 5;
    }
  }

  &__museum--branches {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 4 / 7;
    }
    @include onDesktop {
      grid-column: span 6;
    }
  }
}
